import Box from '@mui/material/Box';
import Button from '@mui/material/Button';

import React from "react";


const listOfTargets = [
    { id: 0, name: "–ó–∞—Ä–∞–±–æ—Ç–∞—Ç—å –Ω–∞ –º–∞—à–∏–Ω—É" },
    { id: 1, name: "–ü–æ–≥–∞—Å–∏—Ç—å –∫—Ä–µ–¥–∏—Ç—ã" },
    { id: 2, name: "–ü–æ–ª—É—á–∏—Ç—å –ø—Ä–µ–º–∏—é" },
    { id: 3, name: "–ó–∞—Ä–∞–±–æ—Ç–∞—Ç—å –Ω–∞ –∫–≤–∞—Ä—Ç–∏—Ä—É" },
    { id: 4, name: "–ó–∞–ø–ª–∞—Ç–∏—Ç—å –Ω–∞–ª–æ–≥–∏" },
    { id: 5, name: "–ù–∞—É—á–∏—Ç—å—Å—è –∏–≤–µ—Å—Ç–∏—Ä–æ–≤–∞—Ç—å" },
    { id: 6, name: "–í–ª–æ–∂–∏—Ç—å—Å—è –≤ –Ω–µ–¥–≤–∏–∂–∏–º–æ—Å—Ç—å" },
    { id: 7, name: "–†–∞–∑–±–æ–≥–∞—Ç–µ—Ç—å" }
];


function Targets() {
    const [currentTarget, setCurrentTarget] = React.useState([]);

    const buttonHandler = (task) => {
        const isInList = !!currentTarget.find((item) => item.id === task.id);
        const newList = isInList
            ? currentTarget.filter((item) => item.id !== task.id)
            : [...currentTarget, task];
        setCurrentTarget(newList);
    };

    return (
        <div>
            <Box sx={{ '& button': { m: 1 }, margin: '2em' }}>
                <div>
                    {listOfTargets.map((task) => (
                        <Button key={task.id} onClick={() => buttonHandler(task)} variant="contained" size="large">
                            {task.name}
                        </Button>
                    ))}
                </div>
            </Box>
            <div>
                {currentTarget.map((item, index) => (
                    <div key={item.id}>
                        {index + 1}. {item.name}
                    </div>
                ))}
            </div>
        </div>
    );
};

export default Targets;