import React from "react";
import TextField from '@mui/material/TextField';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import classes from './Calculator.module.scss';

const theme = createTheme({
    palette: {
        background: { default: "#161616" },
        text: { primary: "rgb(255, 255, 255)", secondary: "rgba(255, 255, 255, .5)" }
    },
});

function Calculator() {

    const [savings, setSavings] = React.useState([1600000]);

    function changeSavings(e) {
        setSavings(e.target.value);
    }

    function onlyNumbers(e) {
        if (!/[0-9]/.test(e.key)) {
            e.preventDefault();
        }
    }

    return (
        <ThemeProvider theme={theme}>
            <div className={classes.container}>
                <div className={classes.wrapper}>
                    <h2 className={classes.title}>–ü–µ—Ä–≤–æ–Ω–∞—á–∞–ª—å–Ω—ã–π –≤–∑–Ω–æ—Å</h2>
                    <p className={classes.body}>–ü—Ä–µ–∂–¥–µ —á–µ–º –≤–∑—è—Ç—å –∏–ø–æ—Ç–µ–∫—É –Ω–∞ –∫–≤–∞—Ä—Ç–∏—Ä—É,
                        –Ω—É–∂–Ω–æ –Ω–∞–∫–æ–ø–∏—Ç—å –Ω–∞ –ø–µ—Ä–≤–æ–Ω–∞—á–∞–ª—å–Ω—ã–π –≤–∑–Ω–æ—Å ‚Äî
                        –æ–±—ã—á–Ω–æ 15% –æ—Ç —Å—Ç–æ–∏–º–æ—Å—Ç–∏ –∂–∏–ª—å—è.</p>
                    <div className={classes.input}>
                        <TextField id="outlined-basic" label="–ù–∞–∫–æ–ø–ª–µ–Ω–∏—è" variant="outlined" value={savings} onChange={changeSavings} onKeyPress={onlyNumbers} type="text" />
                        <p>‚ÇΩ</p>
                    </div>
                </div>
            </div>
        </ThemeProvider>
    );
}

export default Calculator;